import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { 
  Search, 
  Filter, 
  Eye,
  Calendar,
  ChevronLeft,
  ChevronRight
} from 'lucide-react';

interface Candidate {
  id: number;
  full_name: string;
  name?: string;
  telegram_username?: string;
  email?: string;
  status: string;
  last_action_date: string;
  last_action_type?: string;
}

const PAGE_SIZE = 10;

const CandidatesList: React.FC = () => {
  const [candidates, setCandidates] = useState<Candidate[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [statusFilter, setStatusFilter] = useState('');
  const [showFilters, setShowFilters] = useState(false);

  const [page, setPage] = useState(1);
  const [total, setTotal] = useState(0);

  useEffect(() => {
    fetchCandidates();
    // eslint-disable-next-line
  }, [page, searchTerm, statusFilter]);

  const fetchCandidates = async () => {
    try {
      setLoading(true);
      setError(null);
      
      const params = new URLSearchParams();
      if (searchTerm) params.append('search', searchTerm);
      if (statusFilter) params.append('status', statusFilter);
      params.append('skip', String((page - 1) * PAGE_SIZE));
      params.append('limit', String(PAGE_SIZE));
      
      const url = `/api/candidates/?${params}`;
      const response = await fetch(url);
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const data = await response.json();
      setCandidates(data);
      
      //    
      const countParams = new URLSearchParams();
      if (searchTerm && searchTerm.trim()) countParams.append('search', searchTerm);
      if (statusFilter && statusFilter.trim()) countParams.append('status', statusFilter);
      const countUrl = `/api/candidates/count${countParams.toString() ? '?' + countParams.toString() : ''}`;
      const countRes = await fetch(countUrl);
      if (countRes.ok) {
        const countData = await countRes.json();
        setTotal(countData.total);
      }
    } catch (error) {
      console.error('  :', error);
      setError(error instanceof Error ? error.message : ' ');
    } finally {
      setLoading(false);
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case '': return 'status-passed';
      case ' ': return 'status-rejected';
      default: return 'status-waiting';
    }
  };

  const getStatusEmoji = (status: string) => {
    switch (status) {
      case '': return '';
      case ' ': return '';
      default: return '';
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="spinner"></div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="text-center">
          <div className="text-error text-headers font-bold mb-4">  </div>
          <div className="text-main text-background-2 mb-6">{error}</div>
          <button 
            onClick={fetchCandidates}
            className="btn btn-primary"
          >
             
          </button>
        </div>
      </div>
    );
  }

  const totalPages = Math.ceil(total / PAGE_SIZE);

  return (
    <div className="space-y-4 mobile-padding" style={{ paddingBottom: '100px' }}>
      {/* Header */}
      <div className="flex items-center justify-between mb-6">
        <h1 className="text-xl font-bold text-gray-900"></h1>
        <div className="text-sm text-gray-500 bg-gray-100 px-3 py-1 rounded-full">
          {total} 
        </div>
      </div>

      {/* Search Bar */}
      <div className="relative mb-6">
        <Search className="absolute left-4 top-1/2 transform -translate-y-1/2 w-5 h-5 text-gray-400 pointer-events-none" />
        <input
          type="text"
          placeholder="  ..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="w-full pl-12 pr-4 py-4 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-base"
          style={{ minHeight: '48px' }}
        />
      </div>

      {/* Filters Toggle */}
      <div className="mb-6">
        <button
          onClick={() => setShowFilters(!showFilters)}
          className="w-full flex items-center justify-between p-4 bg-gray-50 rounded-xl border hover:bg-gray-100 transition-colors"
          style={{ minHeight: '56px' }}
        >
          <span className="font-medium text-base"></span>
          <Filter className="w-5 h-5" />
        </button>
      </div>

      {/* Filters Panel */}
      {showFilters && (
        <div className="bg-gray-50 rounded-xl p-5 mb-6 space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-3">
              
            </label>
            <select
              value={statusFilter}
              onChange={(e) => setStatusFilter(e.target.value)}
              className="w-full p-4 border border-gray-300 rounded-xl focus:ring-2 focus:ring-blue-500 focus:border-transparent text-base"
              style={{ minHeight: '48px' }}
            >
              <option value=""> </option>
              <option value=""></option>
              <option value=" "> </option>
            </select>
          </div>
          <button
            onClick={() => setShowFilters(false)}
            className="w-full btn btn-primary"
            style={{ minHeight: '48px', padding: '12px 24px' }}
          >
             
          </button>
        </div>
      )}

      {/* Candidates List - Mobile Cards */}
      <div className="space-y-4">
        {candidates.map((candidate) => (
          <div key={candidate.id} className="mobile-card">
            {/* Header with Avatar and Name */}
            <div className="flex items-start space-x-3 mb-3">
              <div className="w-14 h-14 bg-blue-500 rounded-full flex items-center justify-center flex-shrink-0 mt-1">
                <span className="text-white font-bold text-lg">
                  {candidate.full_name.charAt(0)}
                </span>
              </div>
              <div className="flex-1 min-w-0">
                <h3 className="font-semibold text-gray-900 text-lg leading-tight mb-1">
                  {candidate.full_name}
                </h3>
                <p className="text-sm text-gray-500">
                  ID: {candidate.id}
                </p>
              </div>
            </div>
            
            {/* Status Badge - Separate Row */}
            <div className="flex justify-end mb-4">
              <span className={`status-badge ${getStatusColor(candidate.status)} text-sm px-4 py-2 rounded-full font-medium inline-flex items-center space-x-1`}>
                <span>{getStatusEmoji(candidate.status)}</span>
                <span>{candidate.status}</span>
              </span>
            </div>

            <div className="space-y-3 mb-4">
              {candidate.telegram_username && (
                <div className="flex items-center text-sm text-gray-600 bg-gray-50 p-3 rounded-lg">
                  <span className="font-medium mr-2">Telegram:</span>
                  <span className="truncate">{candidate.telegram_username}</span>
                </div>
              )}
              {candidate.email && (
                <div className="flex items-center text-sm text-gray-600 bg-gray-50 p-3 rounded-lg">
                  <span className="font-medium mr-2">Email:</span>
                  <span className="truncate">{candidate.email}</span>
                </div>
              )}
              <div className="flex items-center text-sm text-gray-600 bg-gray-50 p-3 rounded-lg">
                <Calendar className="w-4 h-4 mr-2" />
                <span>{new Date(candidate.last_action_date).toLocaleDateString()}</span>
              </div>
            </div>

            <div className="flex justify-end">
              <Link
                to={`/candidate/${candidate.id}`}
                className="btn btn-primary flex items-center space-x-2"
                style={{ minHeight: '44px', padding: '12px 20px' }}
              >
                <Eye className="w-4 h-4" />
                <span></span>
              </Link>
            </div>
          </div>
        ))}
      </div>

      {/* Empty State */}
      {candidates.length === 0 && !loading && (
        <div className="text-center py-12">
          <div className="text-gray-400 text-6xl mb-4"></div>
          <h3 className="text-lg font-medium text-gray-900 mb-2">  </h3>
          <p className="text-gray-500">
            {searchTerm || statusFilter ? '  ' : '  '}
          </p>
        </div>
      )}

      {/* Pagination */}
      {totalPages > 1 && (
        <div className="mobile-pagination" style={{ marginBottom: '20px', paddingBottom: '20px' }}>
          <button
            className="btn btn-outline touch-target"
            onClick={() => setPage(page - 1)}
            disabled={page === 1}
            style={{ minWidth: '48px', minHeight: '48px', padding: '12px' }}
          >
            <ChevronLeft className="w-5 h-5" />
          </button>
          
          <div className="flex items-center space-x-2">
            <span className="text-sm text-gray-600 px-4 py-2 font-medium">
              {page}  {totalPages}
            </span>
          </div>
          
          <button
            className="btn btn-outline touch-target"
            onClick={() => setPage(page + 1)}
            disabled={page === totalPages}
            style={{ minWidth: '48px', minHeight: '48px', padding: '12px' }}
          >
            <ChevronRight className="w-5 h-5" />
          </button>
        </div>
      )}
    </div>
  );
};

export default CandidatesList; 